import React from 'react';
import {Recording,Stream,Live,Distr,Video,Radio} from "../../Img-bundle";
import {ENGLISH} from "../../redux/Settings-reducer";

function License({stream,distribute,video, radio, language}) {
    let RuVideo
    let RuDistribute = `${distribute} `
    let RuRadio = `     (${radio} )`
    if(language!==ENGLISH){
        if(video==1){RuVideo='1 '}
        if(video>1){RuVideo=`${video} `}
        if(video=='Unlimited'){RuVideo=`  `}
        if(distribute=='Unlimited'){RuDistribute ='  '}
        if(radio===1){RuRadio=`     (${radio} )`}
        if((radio<5)&&(radio!==1)){RuRadio=`     (${radio} )`}
        if(radio>5){RuRadio=`     (${radio} )`}
        if(radio=='Unlimited'){RuRadio =`     (   )`}
    }
    return (
            <div className="license__description ">
                            <div className="description-row">
                                <div className="description-item"><img src={Recording} alt=""/>{language==ENGLISH?'Used for Music Recording':'   '}</div>
                                <div className="description-item"><img src={Stream} alt=""/>{stream} {language==ENGLISH?'Online Audio Streams':'  '}</div>
                                <div className="description-item"><img src={Live} alt=""/>{language==ENGLISH?'For Profit Live Performances':'   '}</div>
                            </div>
                            <div className="description-row">
                                <div className="description-item"><img src={Distr} alt=""/>{language==ENGLISH?`Distribute up to ${distribute} copies`:RuDistribute}</div>
                                <div className="description-item"><img src={Video} alt=""/>{language==ENGLISH?`${video} Music Video`:RuVideo}</div>
                                <div className="description-item"><img src={Radio} alt=""/>{language==ENGLISH?`Radio Broadcasting rights (${radio} Stations)`:RuRadio}</div>
                            </div>
                        </div>
    );
}



export default License

